.container {
  height: 45vh;
  background-color: #24272b;
  display: flex;
  justify-content: center;
}

.price {
  width: 80%;
  background-color: #fefefe;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
}

.price div.discount {
  width: 30%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  border-right: 1px solid rgba(108, 106, 106, 0.6);
}
.price div.discount p,
.price div.discount span {
  text-align: center;
  font-size: 2.3rem;
  color: #24272b;
  font-weight: 500;
}

.price div.discount span {
  font-size: 5rem;
  color: #07070a;
}

.price div.real-price {
  width: 70%;
  height: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.price div.real-price h3 {
  color: #24272b;
  font-size: 2.3rem;
  vertical-align: text-bottom;
  font-weight: 500;
}

.price div.real-price p {
  color: rgba(36, 39, 43, 0.6);
  font-size: 1rem;
}

.price div.real-price span {
  font-size: 4rem;
  font-weight: 700;
  color: #07070a;
}

.btn {
  font-size: 1.54rem;
  font-weight: 700;
  outline: none;
  border: none;
  font-family: 'Teko', sans-serif;
  color: #fefefe;
  padding: 0.3rem 0;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in;
  cursor: pointer;
  width: 30%;
  background-color: #004ba8;
}

.btn:hover {
  background-color: #013d87;
}
