.header {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 8vh;
  background-color: #24272b;
  color: #fefefe;
}

.session-items {
  border-left: 1px solid #6c6a6a;
  border-right: 1px solid #6c6a6a;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-list {
  text-decoration: none;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.header-item.header-principal {
  margin-right: 3rem;
}

.header-item .header-title.principal-name {
  font-size: 2.7rem;
  margin: 0;
  align-self: center;
}

.header-item.header-search {
  height: 100%;
  display: flex;
  border-left: 1px solid #6c6a6a;
  border-right: 1px solid #6c6a6a;
  padding: 0 1rem;
}

.header-title {
  font-size: 3rem;
  text-decoration: none;
  color: inherit;
}

.header-title {
  font-size: 3rem;
  text-decoration: none;
  color: inherit;
}

.logged-user {
  display: flex;
  font-size: 1.4rem;
  font-weight: 300;
  text-transform: uppercase;
  padding: 0 1rem;
  cursor: pointer;
  margin: auto 0;
}

.logged-container {
  display: flex;
  height: 100%;
}

.header-item.session-items.logged {
  display: block;
  margin: auto 0;
}
