.img-container {
  width: 100%;
  margin: 0;
  padding: 0;
  margin-bottom: 3rem;
}

.img-container .img-banner {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url('../../../assets/images/background-navbar.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
