.title {
  font-size: 4rem;
  font-weight: 700;
  margin-left: 3rem;
  text-transform: uppercase;
}

.container {
  margin-top: 3rem;
  color: #24272b;
}

.table {
  padding: 1rem;
}

.response {
  margin-top: 3rem;
  padding: 0 2rem;
  color: #07070a;
  width: 100%;
  height: 80vh;
}

.result {
  border: 1px solid rgba(74, 82, 90, 0.1);
  color: rgba(74, 82, 90, 0.3);
  font-size: 1.32rem;
  padding: 0.3rem 0.65rem;
}

.pagination {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 1.32rem;
}
