.modal {
  position: fixed;
  inset: 0;
  padding: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(1px);
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.modal::-webkit-scrollbar {
  display: none;
}

.modal .modal-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.modal .modal-container::-webkit-scrollbar {
  display: none;
}

.modal .modal-content {
  width: 32%;
  padding: 1rem;
  margin: 1rem;
  background-color: #fefefe;
  color: #24272b;
  border-radius: 2px;
}

.modal .modal-container .modal-content .modal-title {
  text-align: center;
  margin: 0;
  font-size: 3.5rem;
  text-transform: uppercase;
  margin: 1rem 0 0;
  font-weight: 700;
}

.modal .modal-container .modal-content .modal-description.disabled {
  display: none;
}

.modal .modal-close {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  cursor: pointer;
}

.modal .modal-close .modal-close-item {
  font-size: 1.6rem;
  color: #4a525a;
}
