.logout {
  background-color: #24272b;
  padding: 3rem 0.4rem 0.4rem;
  display: flex;
  align-items: flex-end;
  color: #fefefe;
}

.logout > .btn {
  background-color: transparent;
  color: #fefefe;
  outline: none;
  border: none;
  font-family: 'Teko', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.83rem;
  cursor: pointer;
}

