.btn {
  outline: none;
  border: none;
  background-color: transparent;
  color: #fefefe;
  font-size: 1.4rem;
  font-weight: 500;
  cursor: pointer;
}

.btn.btn-login,
.btn.btn-register,
.btn.btn-search {
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 700;
}

.header-item .btn.btn-search {
  display: flex;
  align-items: center;
  height: 100%;
  text-decoration: none;
  color: inherit;
  padding-left: 1rem;
  border-left: 1px solid #6c6a6a;
}

.header-item .btn.btn-dashboard {
  text-decoration: none;
  display: flex;
  height: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  font-weight: 300;
  text-transform: uppercase;
  padding-right: 1rem;
}
