.btn {
  font-size: 1.08rem;
  color: #24272b;
  border: none;
  padding: 0.9rem 0.4rem;
  text-transform: uppercase;
  font-weight: 500;
  transition: background-color 0.2s ease-in;
  cursor: pointer;
  background-color: transparent;
}

.prev {
  transform: rotate(30deg);
  font-size: 0.8rem;
}

.next {
  transform: rotate(-30deg);
  font-size: 0.8rem;
}
