.title {
  font-size: 1.23rem;
}

.prices,
.combobox {
  display: flex;
  flex-direction: column;
}

.combobox {
  margin-bottom: 1rem;
}

.prices div {
  display: flex;
  justify-content: flex-start;
  gap: 0.4rem;
}

.prices > div input {
  width: 40%;
  outline: none;
}

.options {
  display: flex;
  flex-direction: column;
}

.options li {
  display: flex;
  gap: 1rem;
}

.options li svg {
  color: #fefefe;
  cursor: pointer;
}

.options li svg path {
  color: rgba(36, 39, 43, 1);
}
