.container {
  display: flex;
  padding: 3rem 3rem 0;
  justify-content: space-evenly;
  align-items: center;
  color: #24272b;
}

.container section {
  width: 50%;
  height: auto;
}

.container .title {
  font-size: 4rem;
  color: #24272b;
  text-transform: uppercase;
  margin: 0;
  font-weight: 600;
}

.container .subtitle {
  color: #24272b;
  font-size: 1.5rem;
  font-weight: 500;
}

.container .description {
  color: #24272b;
  font-size: 1.3rem;
  font-weight: 300;
  margin: 1rem 0 2rem;
}

.container img {
  width: 25%;
}

.btn {
  background-color: #004ba8;
  padding: 1rem 1.13rem;
  margin-top: 2rem;
  font-size: 1.32rem;
  text-decoration: none;
  color: #fefefe;
  font-family: 'Teko', sans-serif;
  text-transform: uppercase;
  border-radius: 2px;
}
