.main-footer {
  width: 100%;
  height: 35vh;
  background-color: #24272b;
  margin-top: 2rem;
  display: flex;
  padding: 3rem 3rem 0.7rem;
}

.banner-title-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.banner-title.esasteam-title {
  background-color: #004ba8;
  margin: 0;
  font-size: 4.5rem;
  padding: 0 1rem;
  color: #fefefe;
  margin-right: 1rem;
}

.esasteam-footer-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fefefe;
}

.footer-info-page {
  width: 100%;
}

.footer-info-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: center;
  align-content: center;
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  color: rgba(254, 254, 254, 0.6);
  cursor: pointer;
}

.footer-info-list .footer-info-item {
  font-size: 1.3rem;
}

.social-media .social-media-item {
  margin: 0.5rem;
  color: #4a525a;
  cursor: pointer;
}

.footer-info-terms {
  cursor: pointer;
  width: 100%;
}

.footer-list-terms {
  font-size: 0.9rem;
  display: flex;
  justify-content: space-around;
  text-transform: capitalize;
  color: rgba(254, 254, 254, 0.4);
}
