.search {
  padding: 0.32rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-bottom: 2px solid #24272b;
}

.search .input {
  outline: none;
  border: none;
  padding: 1.12rem 1rem 0;
  width: 100%;
  font-size: 1.83rem;
  text-transform: uppercase;
  color: #24272b;
  font-weight: 600;
}

.search .icon {
  color: #24272b;
  margin: 0.32rem;
  font-size: 1.231rem;
}

.search .input::placeholder {
  text-transform: uppercase;
}
