.area {
  width: 100%;

  outline: none;
  border: 1px solid rgba(74, 82, 90, 0.4);
  font-size: 1.12rem;
  font-weight: 500;

  padding: 0.6rem 1rem;
  transition: color 0.2s ease;

  color: rgba(74, 82, 90, 0.7);
  border-radius: 5px;
}

.area::placeholder {
  color: rgba(74, 82, 90, 0.7);
  text-transform: capitalize;
}

.area:focus {
  border: 1px solid rgb(74, 82, 90);
}

.area:focus::placeholder {
  color: transparent;
  font-size: 1rem;

  transition: color 0.2s ease;
}

.container {
  display: flex;
  margin-bottom: 1.43rem;
}
