.container {
  height: 130vh;
  position: relative;
  padding: 4rem 5rem 0;
  display: flex;
}

.img-bg {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container div .img {
  width: 90%;
  height: auto;
}

.loader {
  height: 100vh;
}
