.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.principal-form-container {
  padding: 0 2rem;
  margin-top: 2rem;
}

.form-subtitle {
  text-align: center;
  margin: 0;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 300;
}

.form-description {
  text-align: center;
  margin: 0;
  font-size: 1.15rem;
  font-weight: 300;
  color: rgba(36, 39, 43, 0.7);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
