.card-container {
  padding: 1rem 0;
  flex: 0 0 20%;
  cursor: pointer;
}

.game-thumbnail {
  width: 100%;
  transition: transform 0.3s ease-out;
  box-shadow: 0 7px 17px -8px rgb(0 0 0 / 80%);
}

.game-thumbnail:hover {
  transform: translateY(-15px);
}

.game-thumbnail:hover + .game-title {
  color: #004ba8;
}

.game-title {
  color: #07070a;
  font-weight: 500;
  margin: 0;
  font-size: 1.58rem;
}

.game-creator {
  color: #4a525a;
  margin: 0;
  font-size: 1rem;
  font-weight: 300;
}

@media (min-width: 320px) {
  .card-container {
    flex: 0 0 50%;
  }
}

@media (min-width: 641px) {
  .card-container {
    flex: 0 0 40%;
  }
}

@media (min-width: 768px) {
  .card-container {
    flex: 0 0 30%;
  }
}

@media (min-width: 1024px) {
  .card-container {
    flex: 0 0 22%;
  }
}
