.form-message {
  color: #4a525a;
  text-align: justify;
  font-size: 1.1rem;
  margin: 0;
}

.form-message.end {
  text-align: end;
}

.form-message.center {
  text-align: center;
}

.form-message button {
  font-family: 'Teko', sans-serif;
  outline: none;
  border: none;
  cursor: pointer;
  display: inline;
  text-decoration: none;
  color: #004ba8;
  padding: 0;
  margin: 0;
  font-size: 1.1rem;
}

.form-message a {
  cursor: pointer;
  display: inline;
  text-decoration: none;
  color: #004ba8;
}

.form-message button:hover,
.form-message a:hover {
  text-decoration: underline;
}
