.container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}

.btn {
  padding: 0.5rem;
  background-color: #24272b;
  color: rgba(254, 254, 254, 0.9);
  font-size: 2.231rem;
  outline: none;
  border: none;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3 ease;
}

.btn:hover {
  background-color: #202326;
  transition: background-color 0.3 ease;
}
