.game-section {
  padding: 0 2rem;
  color: #07070a;
}

.game-section-title {
  font-size: 2.5rem;
  font-weight: 300;
  color: #07070a;

  text-transform: uppercase;
  margin: 0;
}

.game-section-cards {
  width: 100%;
  display: flex;
  overflow-x: scroll;
  align-content: space-around;
  align-items: center;
  grid-template-columns: 1fr;
  gap: 1rem;
  margin-bottom: 3rem;
  -ms-overflow-style: none;
  scrollbar-width: 10px;
}

.game-section-cards::-webkit-scrollbar {
  height: 6px;
  width: 6px;
  background-color: transparent;
  cursor: pointer;
}

.game-section-cards::-webkit-scrollbar-track {
  background-color: rgba(201, 200, 199, 0.4);
  height: 6px;
  width: 6px;
  cursor: pointer;
}

.game-section-cards::-webkit-scrollbar-thumb {
  height: 6px;
  background-color: #004ba8;
  border-radius: 6px;
  width: 3px;
  cursor: pointer;
}

